@import 'Variables';

.Projects {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;

    &::-webkit-scrollbar {
        width: 10px;
        opacity: 1;
    }
    
    &::-webkit-scrollbar-thumb{
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: $Theme-ColorGreen;
    }
    
    &-Container {
        display: flex;
        flex-direction: column;
        margin: 10vh 10% 30% 10%;
    }

    &-Title {
        color: $Theme-ColorGreen;
        margin-bottom: 25px;
    }

    &-List {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        gap: 3rem;

        &-Item {
            display: flex;
            align-items: center;
            justify-content: center;
            text-decoration: none;
            color: $Theme-ColorGreen;
            background-color: $Effect-Color;
            min-height: 120px;
            border-radius: 2rem;
            box-shadow: 0 10px 10px rgba($color: #000000, $alpha: 0.2), 0px 0px 20px rgba(#000000, $alpha: 0.2);
            cursor: pointer;

            &-LM {
                color: $Theme-ColorBlue;
                background-color: $Effect-Color-LM;
            }
        }
    }
    
}