@import url(https://fonts.googleapis.com/css2?family=Saira:wght@400;700&display=swap);
.App{position:relative;height:100vh;overflow:hidden}.DarkMode-btn{display:flex;position:absolute;right:20px;top:20px;cursor:pointer;border:none;font-size:1em;width:1em;height:1em;background:transparent;transition:all .2s ease-in-out}.DarkMode-btn:hover,.DarkMode-btn:focus{transform:scale(1.4)}.DarkMode-btn:focus{outline:none;transform:scale(1.4)}.Background{display:flex;align-items:center;justify-content:center;width:100%;height:100vh;background-color:#0d1626;transition:background-color .5s ease}.Background--Light-Mode{display:flex;align-items:center;justify-content:center;width:100%;height:100vh;background-color:#fdfffc;transition:background-color .5s ease}.Background--Light-Mode h1{color:#293AD9}.Background--Light-Mode h3,.Background--Light-Mode p,.Background--Light-Mode li{color:black}.Background--Light-Mode .Mask{background:linear-gradient(45deg, #00ff87, #60efff)}.Mask{width:100%;height:80%;background:linear-gradient(45deg, #293AD9, #29D9B9);-webkit-mask-image:url(/cv/static/media/Background-Lines.76a4d1dc.svg);mask-image:url(/cv/static/media/Background-Lines.76a4d1dc.svg);-webkit-mask-repeat:no-repeat;mask-repeat:no-repeat;-webkit-mask-position:center;mask-position:center;-webkit-mask-size:cover;mask-size:cover}.Fade{position:absolute;bottom:0;left:0;width:calc(100% - 10px);height:15%;background:linear-gradient(rgba(13,22,38,0) 0%, #0d1626 50%)}.Fade--Light-Mode{position:absolute;bottom:0;left:0;width:calc(100% - 10px);height:15%;background:linear-gradient(rgba(253,255,252,0) 0%, #fdfffc 50%)}.h1{letter-spacing:3px}.h3{letter-spacing:2px}.icon{font-size:1em;margin:0 .5em 0 0}@media (min-height: 860px){.icon{font-size:1.5em}}.icon-gsap{display:inline-block;height:1em;width:1em}.icon-contact{font-size:1em}.fa-js{color:#f0db4f}.fa-html{color:#f06529}.fa-css{color:#2965f1}.fa-react{color:#61DBFB}.Link{position:absolute;top:20px;left:20px;text-decoration:none;color:#29D9B9}

.Nav{position:-webkit-sticky;position:sticky;bottom:6vh;display:flex;justify-content:center;width:80%;height:5vw;min-height:50px;max-height:80px;min-width:280px;margin:auto;z-index:10}.Nav-Effect{position:absolute;background-color:rgba(152,159,226,0.3);width:80vw;min-width:270px;height:100%;border-radius:50px;z-index:0}.Nav-Effect--Light-Mode{position:absolute;background-color:rgba(25,106,153,0.3);width:80vw;min-width:270px;height:100%;border-radius:50px;z-index:0}.Nav-Grid{position:absolute;display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-template-areas:"projects skills . contact about";align-items:center;justify-items:center;width:100%;height:100%}.Nav-Link{display:flex;text-decoration:none;color:#FFF;z-index:1;cursor:pointer;transition:all .2s ease-in-out}.Nav-Link:hover,.Nav-Link:focus{transform:scale(1.5)}.Nav-Link-icon{font-size:1.5em}.Nav-HomeBtn{position:absolute}.Nav-HomeBtn img{width:8vw;min-width:50px;max-width:100px}.Nav-HomeScale:hover,.Nav-HomeScale:focus{transform:scale(1.2)}.Nav-Projects{grid-area:projects}.Nav-Skills{grid-area:skills}.Nav-Contact{grid-area:contact}.Nav-About{grid-area:about}

.Skils{position:absolute;top:0px;left:0px;width:97vw;height:97vh;color:white}.Skils-Container{display:flex;padding:10px;flex-direction:column;margin:0 10% 50px 10%;background-color:rgba(196,196,196,0.05);border-radius:1em}@media (max-height: 550px){.Skils-Container{padding-bottom:15vh}}.Skils-Title{color:#29D9B9;margin-top:10vh;margin-left:10%;margin-right:10%;margin-bottom:25px}.Skils-NumberAnimation{margin-left:.5em}.Skils-Skill{margin-bottom:1.5em}@media (max-width: 360px){.Skils-Skill{margin-bottom:1em}}.Skils-Skill-Title{margin-bottom:.2em}.Skils-Bars-Container{background-color:none;display:block;width:100%;height:1rem}@media (max-width: 768px){.Skils-Bars-Container{height:.6em}}@media (max-width: 360px){.Skils-Bars-Container{height:.5em}}.Skils-Bars{border-radius:1em}.Skils-Bars-java{background-color:#f0db4f}.Skils-Bars-html{background-color:#f06529}.Skils-Bars-css{background-color:#2965f1}.Skils-Bars-react{background-color:#61DBFB}.Skils-Bars-gsap{background-color:#08D80C}

.About{position:absolute;top:0px;left:0px;width:100%;height:100%;overflow-y:auto}.About::-webkit-scrollbar{width:10px;opacity:1}.About::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#29D9B9}.About-Container{position:relative;display:grid;color:white;margin:10vh 10% 15% 10%}.About-Container-Header{font-size:2em;color:#29D9B9;margin-bottom:25px}.About-Container-Text{margin-bottom:25px}

.Home{position:absolute;top:0px;left:0px;width:97vw;height:97vh}.Home-Section{display:flex;align-items:center;width:100%;height:80%;color:#FFF;letter-spacing:1px}@media (max-width: 768px){.Home-Section{flex-direction:column;justify-content:center}}.Home-Section-Title{color:#29D9B9;text-align:start;width:50%;font-size:3em;letter-spacing:3px;margin-left:10%;margin-right:30px}@media (max-width: 768px){.Home-Section-Title{margin:20px;width:80%}}@media (max-width: 360px){.Home-Section-Title{font-size:2em}}.Home-Section-Text{margin-right:10%;width:50%;line-height:2em}@media (max-width: 768px){.Home-Section-Text{margin:20px;width:80%}}

.game{display:flex;flex-direction:row;align-items:center;justify-content:center;height:85vh}@media (max-width: 600px){.game{flex-direction:column}}.game-wrapper-left{height:100%;width:50%;display:flex;align-items:center;justify-content:center}@media (max-width: 600px){.game-wrapper-left{height:50%;width:100%}}@media (max-width: 400px){.game-wrapper-left{height:40%;width:100%}}.game-wrapper-right{height:100%;width:50%;display:flex;align-items:center;justify-content:center;padding-bottom:20px}@media (max-width: 600px){.game-wrapper-right{height:50%;width:100%}}@media (max-width: 400px){.game-wrapper-right{height:60%;width:100%}}.game-board{display:flex;flex-direction:column;justify-content:center;align-items:center;width:40vw;height:40vw;margin-left:10px}@media (min-width: 1200px){.game-board{width:30vw;height:30vw}}.game-info{color:#29D9B9;display:flex;flex-direction:column;justify-content:center;width:100%;height:100%;margin:0 10px 0 10px}.game-info-status{font-size:1em;font-weight:bold;margin:0 auto 20px auto}.game-info ol{display:flex;flex-wrap:wrap;justify-content:center}.game-info-buttons{display:flex;justify-content:center;flex-wrap:wrap}.game-info-buttons button{cursor:pointer}.game-info li{list-style:none}.game-info button{font-size:.6em;width:8em;margin:.6em;padding:.5em;color:#29D9B9;background-color:transparent;border:solid #29D9B9;border-radius:10px}.board-row{height:33.334%;width:100%;border-bottom:4px solid #fdfffc}.board-row:after{clear:both;content:"";display:block}.board-row:last-child{border-bottom:none}.board-row--Light-Mode{height:33.334%;width:100%;border-bottom:4px solid #0d1626}.board-row--Light-Mode:after{clear:both;content:"";display:block}.board-row--Light-Mode:last-child{border-bottom:none}.square{background:transparent;color:#29D9B9;border:none;float:left;font-size:1.5em;font-weight:bold;height:100%;width:33.334%;text-align:center;cursor:pointer}@media (min-width: 700px){.square{font-size:2.5em}}.square:nth-child(2){border-left:4px solid #fdfffc;border-right:4px solid #fdfffc}.square--Light-Mode{background:transparent;color:#29D9B9;border:none;float:left;font-size:1.5em;font-weight:bold;height:100%;width:33.334%;text-align:center;cursor:pointer}@media (min-width: 700px){.square--Light-Mode{font-size:2.5em}}.square--Light-Mode:nth-child(2){border-left:4px solid #0d1626;border-right:4px solid #0d1626}.lumenate{color:#fc7beb}.square:focus{outline:none}.go-to-move{text-decoration:none;cursor:pointer}.go-to-move:focus{font-weight:bold}

.Projects{position:absolute;top:0px;left:0px;width:100%;height:100%;overflow-y:auto;overflow-x:hidden}.Projects::-webkit-scrollbar{width:10px;opacity:1}.Projects::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#29D9B9}.Projects-Container{display:flex;flex-direction:column;margin:10vh 10% 30% 10%}.Projects-Title{color:#29D9B9;margin-bottom:25px}.Projects-List{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:3rem;gap:3rem}.Projects-List-Item{display:flex;align-items:center;justify-content:center;text-decoration:none;color:#29D9B9;background-color:rgba(152,159,226,0.3);min-height:120px;border-radius:2rem;box-shadow:0 10px 10px rgba(0,0,0,0.2),0px 0px 20px rgba(0,0,0,0.2);cursor:pointer}.Projects-List-Item-LM{color:#293AD9;background-color:rgba(25,106,153,0.3)}

.Contact{position:absolute;top:0px;left:0px;width:97vw;height:97vh;display:flex;flex-direction:column}.Contact-Title{color:#29D9B9;margin:100px 0 50px 10%}@media (max-width: 300px){.Contact-Title{margin:100px auto 50px auto}}.Contact-Info{color:white;list-style:none;margin-left:10%;line-height:4rem}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html, body {
    touch-action: none;
    -ms-touch-action: none;
    overflow: hidden;
    position: relative;
    height: 100%;
  }
  
  body {
    font-family: 'Saira', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
  }

  img {
    display: block;
  }

  @media screen and (min-width: 320px) {
   body {
     /*calc([minimum size]+([maximum size]-[minimum size])*((100vw-[minimum viewport width])/([maximum viewport width]-[minimum viewport width])));*/
      font-size: calc(16px + 6 * ((100vw - 320px) / 680));
    }
  }
  @media screen and (min-width: 1000px) {
   body {
      font-size: 22px;
    }
  }
